@import url(https://fonts.googleapis.com/css2?family=Baloo+2:wght@400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Unica+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  background-color: rgb(233, 233, 233);
}

.app{font-family:"Baloo 2",cursive;background-color:#e9e9e9}.app__container{display:flex}
#topbar .topbar__logo{font-weight:bold;font-size:2.2rem;font-family:"Unica One",cursive;letter-spacing:-0.24rem;color:#fff;margin-right:12rem}#topbar .topbar__button{width:2.6rem;height:2.6rem}#topbar .topbar__note-input{margin-top:.4rem;margin-left:.5rem}#topbar .btn:active{outline:none;background-color:#ac8891;color:#fff}#topbar .btn:hover{color:#fff}#topbar .btn-secondary{background-color:gray}#topbar .navbar{height:4rem;background-color:#adadad}#topbar .form-inline{margin-right:.3rem}
.sidebar{background-color:#a78b92;width:26%;height:96.8vh;padding-top:2rem}.sidebar__profile{display:flex;flex-direction:column;align-items:center;justify-content:space-between;margin-bottom:2rem}.sidebar__avatar{background-color:#ccc;width:7rem;height:7rem;border-radius:50%}.sidebar__email,.sidebar__name{color:#fff;font-size:1.3rem;margin:0}
#folders .folders{font-size:1.3rem;margin-top:1rem}#folders .folders__title-container{padding:0 1.2rem;font-size:1.3rem;background-color:gray}#folders .folders__title{font-size:1.6rem;font-weight:600;margin-top:-0.6rem;color:#fff;padding-top:.5rem}#folders .folders__new-folder{display:flex;padding-bottom:1.5rem}#folders .folders__input{width:18vw;height:2.5rem;border-radius:.25rem;border:none;outline:none;padding-left:10px}#folders .folders__new-btn{margin:0}#folders .folders .btn:active{outline:none;background-color:#ac8891;color:#fff}#folders .folders .btn:hover{color:#fff}#folders .folders__wrapper{display:flex;justify-content:space-between;height:1.5rem;align-items:center;border-radius:0;font-weight:500}#folders .folders__folder-name{font-size:1.3rem;font-weight:600}#folders .folders .folders__name-arrow{width:1.5rem;height:1.5rem;border-radius:.25rem;line-height:2.4rem}#folders .folders .list-group-item{display:flex;flex-direction:column}#folders .folders__notes-list{list-style-type:none;padding-left:.4rem}#folders .folders__notes-item{border-top:1px solid gray;margin-top:.7rem;padding-top:.25rem;color:#9c9c9c;font-weight:600}
p{margin:.45rem 0}.note{box-sizing:unset;margin-left:1rem;max-width:unset;width:71vw}.note__header{padding-top:.5rem;padding-left:1rem;padding-right:1rem;background-color:#d1c6c9;height:2.4rem}.note__title{outline:none;width:100%;border:none;border-radius:.3rem;border-bottom-left-radius:0;border-bottom-right-radius:0;font-weight:500}.note__title:hover{cursor:text}.note__content{outline:none;font-size:1.15rem;width:100%;border-radius:.3rem;border:none}.btn{background-color:#a8717f;color:#fff;border:none}.close{margin:0;position:absolute;right:1rem;top:.5rem}.btn-secondary{background-color:gray}.modal-header{padding:0}.modal-body{padding-top:0;margin-top:.4rem;padding-right:2rem}
