@import '../../../partials/mixins';
@import '../../../partials/variables.scss';

#folders {
  .folders {
    font-size:1.3rem;
    margin-top : 1rem;
  
    &__title-container{
      padding : 0 1.2rem;
      font-size:1.3rem;
      background-color: grey;
    }
  
    &__title {
      font-size: 1.6rem;
      font-weight:600;
      margin-top: -0.6rem;
      color: white;
      padding-top : 0.5rem;
    }

    &__new-folder {
      display : flex;
      padding-bottom:1.5rem;
    }

    &__input {
      width:18vw;
      height:2.5rem;
      border-radius : 0.25rem;
      border:none;
      outline : none;
      padding-left : 10px;
    }

    &__new-btn {
      margin:0;
    }

    .btn:active{
      outline : none;
      background-color:#ac8891;
      color:white;
    }
  
    .btn:hover {
      color:white;
    }

    &__wrapper {
      display: flex;
      justify-content: space-between;
      height : 1.5rem;
      align-items : center;
      border-radius : 0;
      font-weight: 500;
    }

    &__folder-name {
      font-size:1.3rem;
      font-weight : 600;
    }

    .folders__name-arrow {
      width:1.5rem;
      height:1.5rem;
      border-radius:0.25rem;
      line-height:2.4rem;
    }

    .list-group-item {
      display: flex;
      flex-direction: column;

    }

    &__notes-list {
      list-style-type: none;
      padding-left:0.4rem;
    }

    &__notes-item {
      border-top : 1px solid grey;
      margin-top: 0.7rem;
      padding-top : 0.25rem;
      color : #9c9c9c;
      font-weight : 600;
    }
  }
}

